<template>
	<div id="app">
		<div class="index container-fluid" v-cloak>
			<div class="row" style="height: 50px;">
				<form class="form" role="form" style="position: fixed; background-color: white">
					<div class="form-group col-md-3 input">
						<div class="input-group" style="cursor: pointer">
							<div class="input-group-addon" style="background-color: #428BCA;color: #fff;"><label
								for="file" style="margin-bottom: 0;cursor: pointer">导入</label></div>
							<input type="file" id="file" class="form-control" @change="importfile()"
								   style="cursor: pointer">
						</div>
					</div>
					<div class="form-group col-md-3">
						<!--<a @click="show()" href="javascript:void(0)" class="btn btn-primary" style="font-weight: bold">显示</a>-->
						<a @click="downloadExl()" href="javascript:void(0)" class="btn btn-primary"
						   style="font-weight: bold">导出</a>
						<!-- <a href="javascript:void(0)" download="这里是下载的文件名.xlsx" id="hf" class="btn btn-primary">下载</a> -->
					</div>
				</form>
			</div>

			<div v-if="data.length" class="row">
				<table class="table table-bordered table-hover text-center">
					<thead>
					<tr>
						<td class="text-center" style="min-width: 120px;">日期</td>
						<td class="text-center" v-for="i in days" style="min-width: 120px;">{{ mouth }}/{{ i }}</td>
					</tr>
					<tr>
						<td class="text-center" style="min-width: 120px;">姓名\星期</td>
						<td class="text-center" v-for="w in weeks" style="min-width: 120px;">{{ w }}</td>
					</tr>
					</thead>
					<tbody>
					<tr v-for="(o, index) in data" key="i">
						<td v-for="(r, i) in o">{{r == '18:00:00' ? r + '(缺卡)' : r }}</td>
					</tr>
					</tbody>
				</table>
			</div>

			<!--<div class="row" style="overflow: auto;margin: 0;">
				<table class="table table-bordered table-hover text-center" v-show="table_row.length">
					<thead>
					<tr>
						<td class="text-center" style="min-width: 120px;">日期</td>
						<td class="text-center" v-for="i in days" style="min-width: 120px;">{{ mouth }}/{{ i }}</td>
					</tr>
					<tr>
						<td class="text-center" style="min-width: 120px;">姓名\星期</td>
						<td class="text-center" v-for="w in weeks" style="min-width: 120px;">{{ w }}</td>
					</tr>
					</thead>
					<tbody>
					<tr v-for="(o,i) in table_tbody" key="i">
						<td v-for="(r,i) in o">{{r == '18:00:00' ? r + '(缺卡)' : r }}</td>
					</tr>
					</tbody>
				</table>
			</div>-->
		</div>
	</div>
</template>

<script>
	import Vue from 'vue'

	export default {
		name: 'app',
		data() {
			return {
				date: "",
				year: "",
				mouth: "",
				days: "",
				weeks: [],
				workbook: "",
				rABS: false,
				excelData: "",
				SheetNames: 1,
				data: [],
				headers: {},
				headers_row: 3,
				table_thead: [],
				name_list: [],
				table_row: [],
				name_value: "",
				first: true,
				table_tbody: [],
				tmpDown: "",
				href: "",
				tmpWB: "",
				tableDataObject: [],
				tableDataArray: [],
			}
		},
		created() {
			this.getDays()
		},
		methods: {
			getWeeks() {
				let myDate = new Date()
				for (let i = 1; i <= this.days; i++) {
					myDate.setFullYear(this.year, this.mouth - 1, i)
					if (myDate.getDay() === 0) {
						this.weeks.push('日')
					}
					else if (myDate.getDay() === 6) {
						this.weeks.push('六')
					}
					else if (myDate.getDay() === 5) {
						this.weeks.push('五')
					}
					else if (myDate.getDay() === 4) {
						this.weeks.push('四')
					}
					else if (myDate.getDay() === 3) {
						this.weeks.push('三')
					}
					else if (myDate.getDay() === 2) {
						this.weeks.push('二')
					}
					else if (myDate.getDay() === 1) {
						this.weeks.push('一')
					}
				}
			},
			getDays() {
				//构造当前日期对象
				this.date = new Date()
				//获取年份
				this.year = this.date.getFullYear()
				//获取上一个月份
				this.mouth = this.date.getMonth()
				//定义当月的天数；this.days
				//当月份为二月时，根据闰年还是非闰年判断天数
				if (this.mouth === 2) {
					this.days = this.year % 4 === 0 ? 29 : 28
				}
				else if (this.mouth === 1 || this.mouth === 3 || this.mouth === 5 || this.mouth === 7 || this.mouth === 8 || this.mouth === 10 || this.mouth === 12) {
					//月份为：1,3,5,7,8,10,12 时，为大月.则天数为31；
					this.days = 31
				}
				else {
					//其他月份，天数为：30.
					this.days = 30
				}
				// console.log(this.days);
				this.getWeeks()
			},
			importfile() {
				const file = document.getElementById('file')
				if (!file.files) {
					return
				}
				const files = file.files[0]
				const reader = new FileReader()
				reader.onload = (e) => {
					let data = e.target.result
					// if(this.rABS) {
					// 	this.workbook = XLSX.read(btoa(this.fixdata(data)), {//手动转化
					// 		type: 'base64'
					// 	})
					// } else {
					this.workbook = window.XLSX.read(data, {
						type: 'binary',
					})
					// }
					const sheetname3 = this.workbook.SheetNames[3]
					const worksheet3 = this.workbook.Sheets[sheetname3]
					this.add(worksheet3)
				}
				reader.readAsBinaryString(files)
			},
			add(sheet) {
				const keys = Object.keys(sheet)
				const rex = new RegExp(/^([a-zA-Z]+)(\d+)$/g)
				console.log(this.tableDataArray)
				keys.filter(k => k[0] !== '!').filter(k => {
					let key = rex.exec(k)
					rex.lastIndex = 0
					let value = sheet[k].v
					if (this.colToIndex(key[1]) > 4) {
						value = value.split('\n')[value.split('\n').length - 1]

						let s = value.split(':')
						if (s[0] && typeof s[0] === 'string') {
							let num = Number(s[0])

							// not NaN
							/* eslint-disable no-self-compare */
							if (num === num && num > 7 && num < 13) {
								value = undefined
							}
						}
					}
					else {
//						debugger
					}
					if (!this.tableDataObject[key[2]]) {
						this.tableDataObject[key[2]] = {}
					}
					this.tableDataObject[key[2]][key[1]] = value
					if (!this.tableDataArray[key[2]]) {
//						this.$set(this.tableDataArray, `${key[2]}`, [])
						this.tableDataArray[key[2]] = []
						Vue.set(this.tableDataArray, `${key[2]}`, [])
					}
//					this.tableDataArray[key[2]][this.colToIndex(key[1])] = value
					this.$set(this.tableDataArray[key[2]], this.colToIndex(key[1]), value)
//					this.tableDataArray[key[2]].splice(this.colToIndex(key[1]), 0, value)
				})
				this.filter()
			},
			colToIndex(str) {
				str = str.toLocaleLowerCase()
				let index = 0
				str.split('').reverse().forEach((v, i) => {
					index += singleIndex(v) * Math.pow(26, i)
				})
//				console.log(str, index)
				return index - 1

				function singleIndex(str) {
					return str.charCodeAt(0) - 96
				}
			},
			filter() {
				window.a = this
				this.data = this.copyData(this.tableDataArray)
				this.data.forEach(item => {
					for (let i = 0; i < this.days + 5; i++) {
						if (!item[i]) {
							item[i] = undefined
						}
					}
					console.log(item)
				})
				this.data = this.data.filter((v, i) => i > 3).map(v => {
					return v.filter((v, i) => i === 0 || i > 4)
				})
			},
			copyData(data) {
				let newData
//				console.log(newData)
				if (data instanceof Array) {
					newData = data.slice()
					newData = newData.map(item => this.copyData(item))
				}
				else if (typeof data === Object && data !== null) {
					// object
				}
				else {
					// string
					return data
				}
				return newData
			},
			downloadExl() {
				let json = this.data
				let type
				let week = []
				week.push('姓名-星期')
				for (let j = 0; j < this.weeks.length; j++) {
					week.push(this.weeks[j])
					if (j === this.weeks.length - 1) {
						console.log(j)
						json.unshift(week)
					}
				}

				let keyMap = ['日期']// 获取键

				// 设置表头
				for (let i = 1; i <= this.days; i++) {
					keyMap.push(this.mouth + '/' + i)
					if (i === this.days) {
						json.unshift(keyMap)
					}
				}

				//用来保存转换好的json
				let tmpdata = []
				json.map((v, i) => keyMap.map((k, j) => Object.assign({}, {
					v: v[j],
					position: (j > 25 ? this.getCharCol(j) : String.fromCharCode(65 + j)) + (i + 1)
				}))).reduce((prev, next) => prev.concat(next)).forEach((v, i) => {
					// console.log(v.position)
					// console.log(v)
					tmpdata[v.position] = {
						v: v.v
					}
				})

				//设置区域,比如表格从A1到D10
				let outputPos = Object.keys(tmpdata)
				this.tmpWB = {
					SheetNames: ['打卡时间'], //保存的表标题
					Sheets: {
						'打卡时间': Object.assign({}, tmpdata, {
							'!ref': outputPos[0] + ':' + outputPos[outputPos.length - 1] //设置填充区域
						})
					}
				}
				this.tmpDown = new Blob([this.s2ab(window.XLSX.write(this.tmpWB,
					{
						bookType: (type === undefined ? 'xlsx' : type),
						bookSST: false,
						type: 'binary'
					}//这里的数据是用来定义导出的格式类型
				))], {
					type: ""
				}) //创建二进制对象写入转换好的字节流
				console.log(this.tmpWB)
				console.log(this.tmpDown)

				window.saveAs(this.tmpDown, '考勤表.xlsx')
			},
			s2ab(s) { //字符串转字符流
				let buf = new ArrayBuffer(s.length)
				let view = new Uint8Array(buf)
				for (let i = 0; i !== s.length; ++i) view[i] = s.charCodeAt(i) & 0xFF
				return buf
			},
			// 将指定的自然数转换为26进制表示。映射关系：[0-25] -> [A-Z]。
			getCharCol(n) {
				let s = ''
				let m = 0
				while (n > 0) {
					m = n % 26 + 1
					s = String.fromCharCode(m + 64) + s
					n = (n - m) / 26
				}
				return s
			},
		},
	}
</script>

<style>
	#app {
	}
</style>



